<template>
    <div> 
        <briup-fullpagelayout title="" @back="backHandler">
            <div class="address" >
                <!--  -->
                <div class="add">
                    <van-cell-group>
                        <van-field v-model="customerId" label="" />
                        <van-field v-model="telephone" label="" />
                        <van-field v-model="province" label="" />
                        <van-field v-model="city" label="" />
                        <van-field v-model="area" label="" />
                        <van-field v-model="address" label="" />
                    </van-cell-group>
                </div>
                <!-- / -->
                <!--  -->
                <van-button @click="backListHandler()" size="large" type="warning"></van-button>
                <!-- / -->
            </div>
        </briup-fullpagelayout>
    </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
export default {
    data() {
        return{
            customerId:'',
            telephone:'',
            province:'',
            city:'',
            area:'',
            address:'',
        }
    },
    computed: {
        ...mapState('user',['info'])
    },
    created() {
        this.customerId = this.info.id,
        this.telephone = this.$route.query.telephone
        this.province = this.$route.query.province
        this.city = this.$route.query.city
        this.area = this.$route.query.area
        this.address = this.$route.query.address
    },
    methods: {
        ...mapActions('address',['findAllAddresses','SaveOrUpdateAddress']),
        // 
        backHandler(){
            this.$router.back();
        },
        // 
        backListHandler(){
            let form = {
                // id:this.$route.query.id,
                customerId:this.customerId,
                telephone:this.telephone,
                province:this.province,
                city:this.city,
                area:this.area,
                address:this.address
            }
            this.$router.push({path:'/manager/address'})
            this.SaveOrUpdateAddress(form)
        }
       
    }
}
</script>