<template>
  <div class="register">
    <briup-fullpagelayout title="" @back="backHandler">
        <!--  -->
        <div class="register_t">
            <van-field
                left-icon="phone-o"
                v-model="telephone"
                label=""
                placeholder=""/>
            <van-field
                left-icon="comment-circle-o"
                v-model="vcode"
                label=""
                placeholder="">
                <van-button slot="button" size="small" type="info" @click="GainVcode"></van-button>
            </van-field>
            <van-field
                left-icon="chat-o"
                v-model="password"
                center
                clearable
                label=""
                placeholder="">
            </van-field>
        </div>
        <!-- / -->
        <div style="text-align:center;padding:1em" >
            <van-button 
                type="info"
                round 
                @click="backHandler" 
                style="width:100%">
            </van-button>
        </div>
    </briup-fullpagelayout>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);
export default {
    data() {
    return {
        vcode:'',
        password:'',
        telephone:''
    };
  },
  computed: {
      ...mapState('register',['vercode','cusregister'])
  },
  methods:{
    ...mapActions('register',['GetVerCode','CustomerRegister']),
    //  
    GainVcode(){
        Toast('');
        this.GetVerCode({telephone:this.telephone})
    },
    // 
    backHandler(){
        this.params = {
            telephone:this.telephone,
            password:this.password,
            vcode:this.vcode
        }
        this.CustomerRegister(this.params)
        this.$router.push({path:'/login'})
    }
  }
}
</script>
<style scoped>
    .register_t{
        margin-top:3em;
    }
</style>