<template>
  <div class="home">
    <!--  -->
    <div class="title">
      <van-row>
        <van-col span="4">
          <van-icon size="30" @click="CloseHome" style="padding:16px" name="cross" color="#333333"/>
        </van-col>
        <van-col :offset="2" span="18" style="font-size:20px;color:#333333;padding:16px;text-align:left">e</van-col>
      </van-row>
    </div>
    <!-- / -->

    <!--  -->
    <header>
      <van-swipe class="my-swipe" :height="260" :autoplay="3000" indicator-color="white">
        <van-swipe-item >
          <img src="../../assets/home.jpg" alt="">
          e1
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/home1.jpg" alt="">
          e2
        </van-swipe-item>
      </van-swipe>
    </header>
    <!-- / -->

    <!--  -->
    <van-grid :column-num="3">
      <van-grid-item
        @click="toProductHandler(c.id,index)"
        v-for="(c,index) in categories.list"
        :key="c.id"
        :icon="c.icon"
        :text="c.name"/>
        <van-grid-item>
          <van-icon 
          @click="ShowCategoriesHandler(obj)"
           name="shop-collect-o" 
           size="2.5em" 
           color="#1989fa" 
           info=""/>
        </van-grid-item>
    </van-grid>
    <!-- {{categories}} -->
    <!-- / -->

    <!--  -->
    <van-grid :column-num="2">
      <van-grid-item
        @click="toProductListHandler(obj)"
        v-for="(p) in products"
        :key="p.id">
        <van-image :src="p.photo" />
        <div>{{p.name}}</div>
      </van-grid-item>
    </van-grid>
    <!-- {{products}} -->
    <!-- / -->
  </div>
</template>

<script>

import {mapState, mapActions} from 'vuex'

export default {
  data (){
    return{
      obj:''
    }
  },
  computed:{
    ...mapState('category',['categories']),
    ...mapState('product',['products']),
  },
  created(){
    this.QueryCategories({page:0,pageSize:5});
    this.QueryProducts({page:0,pageSize:4});
  },
  methods:{
    ...mapActions('user',['logout']),
    ...mapActions('category',['QueryCategories']),
    ...mapActions('product',['QueryProducts']),
    // 
    ShowCategoriesHandler(){
      var obj = {
        id:this.categories.list[0].id,
        activeKey:0
      }
      this.$router.push({path:'/manager/product_list',query:obj})
    },
    //  
    toProductHandler(id,activeKey){
      this.$router.push({path:'/manager/product_list',query:{id,activeKey}})
    },
    //  
    toProductListHandler(id,activeKey){
      var obj = {
        id:this.categories.list[0].id,
        activeKey:0
      }
      this.$router.push({path:'/manager/product_list',query:obj})
      // this.$router.push({path:'/manager/product_list',query:{id,activeKey}})
    },
    // 
    CloseHome(){
      this.logout()
      .then(()=>{
        this.$router.push({path:'/login'})
      })
    }
  }
}
</script>

<style scoped>
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
  overflow: hidden;
}
.header {
  /* width: 2000px; */
  overflow: hidden;
}
.header img {
  width: 100%;
}
</style>