<template>
  <div class="register">
    <briup-fullpagelayout title="" @back="backHandler">
        <van-cell title="" size="large" />
        <van-uploader :after-read="afterRead" />
        <van-cell title="" size="large" />
        <van-uploader :after-read="afterRead" />
        <!--  -->
        <div class="btn" @click="RealnameHandler">
        
        </div>
        <!--  -->
       {{info}}
       
    </briup-fullpagelayout>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import { Toast } from 'vant';
import axios from 'axios'

export default {
    data() {
    return {
        user_id:''
    };
  },
  computed: {
     ...mapState('user',['info']),
     ...mapState('realname',['cusrealname'])
  },
  methods:{
    ...mapActions('realname',['CusomterRealname']),
    // 
    RealnameHandler(){
        this.params = {
            user_id:this.info.id
        }
        // console.log(this.params)
        this.CusomterRealname(this.params)
        Toast.success('');
    },
    afterRead(file) {
      // 
    //   FormData,
    var formData = new FormData();
    formData.append('file1',file.file);
    formData.append('file2',file.file);
    axios.post('http://134.175.100.63:5588/user/realname?user_id=183',formData)
    .then(res => {
        console.log(res.data)
    })
    //   console.log(file);
    },
    // 
    backHandler(){
        this.$router.push({path:'/manager/user'})
    }
  }
}
</script>
<style scoped>
    .btn {
  background-color: #ededed;
  width: 90%;
  margin: 2em auto;
  line-height: 3em;
  text-align: center;
  border: 1px solid #ededed;
  border-radius: 1.5em;
  font-size: 14px;
}
</style>