<template>
  <div class="user">
    <!--  -->
    <div class="title">
      <van-nav-bar title="" />
    </div>
    <!-- / -->
    <!--  -->
    <div class="header">
      <div class="photo">
        <van-uploader :before-read="beforeRead"  >
          <img src="../../assets/user.png" alt=""/>
        </van-uploader>
      </div>
      <div class="name">{{info.name}}</div>
    </div>
    <!-- / -->

    <!--  -->
    <van-cell icon="balance-o" title="" :value="cusInfo.money" is-link to="money" />
    <!-- / -->

    <!--  -->
    <!-- <van-cell icon="manager-o" title="" is-link to="realname" /> -->
    <!-- / -->

    <!--  -->
    <van-cell icon="location-o" title="" is-link to="address" />
    <!-- / -->

    <!--  -->
    <van-cell icon="star-o" title="" is-link @click="toCommentHandler(id)" />
    <!-- / -->
    
    <!--  -->
    <van-cell icon="phone-o" title="" @click="show = true" />
    <van-overlay :show="show" @click="show = false"  >
      <div class="wrapper" @click.stop>
        <div class="block">
          <van-cell title="" size="large" >
          <van-icon
            @click="closeModal()"
            slot="right-icon"
            name="cross"
            style="line-height: inherit;" />
          </van-cell>
          <van-cell title="0471-1234-888" />
        </div>
      </div>
    </van-overlay>
    <!-- / -->
    <!-- {{info}} -->
    <!-- {{cusInfo}} -->
    <!--  -->
    <div class="btn" @click="logoutHandler">
      
    </div>
    <!-- / -->
  </div>
</template>
<script>

import {mapState, mapActions} from 'vuex'
import Vue from 'vue';
import { Overlay } from 'vant';
Vue.use(Overlay);

export default {
  props:['id'],
  data() {
    return {
      show: false
    }
  },
  created(){
    this.FindCustomerById(this.info.id)
  },
  methods:{
    ...mapActions('user',['logout','FindCustomerById']),
    // 
    toCommentHandler(id){
      this.$router.push({path:'/manager/comment',query:id})
    },
    // 
    beforeRead(file) {
      if (file.type !== 'image/jpeg') {
        Toast(' jpg ');
        return false;
      }
      return true;
    },
    asyncBeforeRead(file) {
      return new Promise((resolve, reject) => {
        if (file.type !== 'image/jpeg') {
          Toast(' jpg ');
          reject();
        } else {
          resolve();
        }
      });
    },
    // 
    closeModal(){
      this.show = false
    },
    toAddressHandler(){
      // this.$router.push({path:'/manager/address'})
    },
    logoutHandler(){
      this.logout()
      .then(()=>{
        this.$router.push({path:'/login'})
      })
    }
  },
  computed:{
    ...mapState("user",["info",'cusInfo'])
  }
}
</script>

<style scoped>

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.block {
  width: 240px;
  height: 120px;
  background-color: #fff;
}
.header {
  padding-top: 25px;
  text-align: center;
  margin-bottom:2.5em;
}
.header .photo {
  margin: 0 auto;
  width: 10em;
  height: 10em;
  border-radius: 50%;
  box-sizing: border-box;
  border:1px solid #ccc;
  overflow:hidden;
  padding: .5em;
}
.header .name {
  margin: 0 auto;
  width:200px;
  margin-top: 1.5em;
  line-height: 3em;
  font-size: 14px;
  border:1px solid #ccc;
  border-radius: 10px;
}
.header .photo img {
 width: 100%;
 border-radius: 50%;
}
.btn {
  background-color: #ededed;
  width: 90%;
  margin: 2em auto;
  line-height: 3em;
  text-align: center;
  border: 1px solid #ededed;
  border-radius: 1.5em;
  font-size: 14px;
}

</style>