<template>
    <div class="details">
        <briup-fullpagelayout title="" @back="backHandler">
            <!-- {{JSON.parse($route.query.data).orderLines}} -->
            <!--  -->
            <div class="photo">
                <img src="../../../assets/user.png" alt=""/>
            </div>
            <!-- <div>
            {{JSON.parse($route.query.data).waiter.realname}}
            </div>
            <div >
            {{JSON.parse($route.query.data).waiter.telephone}}
            </div> -->
            <div class="order_details">
                <div>
                <van-row v-for="line of JSON.parse($route.query.data).orderLines.values()" :key="line.productId">
                        <van-col :span="8">{{line.product.name}}</van-col>
                        <van-col :span="8">x{{line.number}}</van-col>
                    </van-row>
                </div>
                <div>{{JSON.parse($route.query.data).total}} </div>
                <div>{{JSON.parse($route.query.data).orderTime | datefmt}} </div>
                <div >
                {{JSON.parse($route.query.data).address.province}}
                {{JSON.parse($route.query.data).address.city}}
                {{JSON.parse($route.query.data).address.area}}
                {{JSON.parse($route.query.data).address.address}}
                </div>
            </div>
            <div class="my_comment">
            <van-button style="margin-left:20em"
                            plain type="warning" 
                            size="mini" 
                            @click="showPopup">
                        </van-button>
            </div>               
            <div class="card1">{{info.id}} & {{info.name}}</div>
            <div class="card2" v-for="c in comments.list" :key='c.id'>
                <!-- {{info}} -->
                <van-row>
                    <van-col span="6">
                        <img style="width:5em" src="../../../assets/user.png" alt=""/>
                    </van-col>
                    <van-col span="18">
                        <div>{{c.orderId}}</div>     
                        <div style="padding-top:.5em;padding-bottom:.5em">{{c.content}}</div>
                        <div>{{c.commentTime | datefmt}}</div>
                    </van-col>
                </van-row>
            </div>
            <!-- {{orders}} -->
        </briup-fullpagelayout>
        <!--  -->
        <van-popup v-model="show" style="width:20em">
            <!--  -->
            <van-field v-model="cusId" label="" />
            <van-field v-model="orderId" label="" />
            <!-- {{content}} -->
            <van-field v-model="content"
                rows="4"
                autosize
                type="textarea"
                label="" 
                maxlength="200"
                show-word-limit
                placeholder="" />
            <van-cell-group>
                <van-button 
                    style="margin:1em 2em"
                    type="info" 
                    size="small" 
                    @click="saveCommentHandler()">
                </van-button>
            </van-cell-group>
        </van-popup>
        <!-- / -->
    </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
export default {
    data(){
        return{
            params: {
                page:0,
                pageSize:100,
            },
            show: false,
            content:'',
            cusId:'',
            orderId:''
        }
    },
    created() {
        this.params = {
            page:0,
            pageSize:100,
            cusId:this.info.id,
            orderid:JSON.parse(this.$route.query.data).id
        }
        this.QueryComment(this.params)

        this.cusId = this.info.id
        this.orderId = JSON.parse(this.$route.query.data).id
    },
    computed: {
        ...mapState('comment',['comments']),
        ...mapState('user',['info']),
    },
    methods:{
        ...mapActions('comment',['QueryComment','SaveComment']),
        // 
        saveCommentHandler(){
            this.show = false;
            let form = {
                cusId:this.cusId,
                orderId:this.orderId,
                content:this.content
            }
            this.SaveComment(form).then((response)=>{
                this.QueryComment(this.params)
            })
            
        },
        // 
        showPopup(){
            this.show = true;
        },
        // 
        backHandler(){
            this.$router.push({path:'/manager/order'})
        }
    }
}
</script>

<style scoped>
.all_comments{
    margin-top:1em;
    padding: .5em;
    border-radius: 5px;
    background: #f1f1f1;
}
.card2{
  margin: .5em 1em;
  padding: .5em;
  border-radius: 5px;
  background: #f1f1f1;
}
.card1{
  margin: 1em;
}
.my_comment{
    margin:1em;
    padding-left: .5em;
}
.card{
    margin-top:1em;
    padding: .5em;
    border-radius: 5px;
    background: #f1f1f1;
}
.order_details{
    margin: 1em 1em;
    padding: .5em;
    border-radius: 5px;
    background: #f1f1f1;
    /* border: 1px solid #f1f1f1; */
}
.photo {
  margin: 0 auto;
  width: 10em;
  height: 10em;
  border-radius: 50%;
  box-sizing: border-box;
  border:1px solid #ccc;
  overflow:hidden;
  padding: .5em;
  margin-top:2em;
  margin-bottom:2em
}
</style>