<template>
  <div class="login">
    <div class="header">
      <div class="logo"></div>
      <div class="title">
        
        <span style="font-size:.8em"></span>
      </div>
    </div>
    <div class="form">
      <van-cell-group>
        <van-field
          v-model="form.username"
          required
          clearable
          label=""
          icon="question-o"
          placeholder=""
        />
        <van-field
          v-model="form.password"
          type="password"
          label=""
          placeholder=""
          required
        />
      </van-cell-group>
      <div style="text-align:center;padding:1em" >
       <van-button 
        plain round 
        @click="loginHandler" 
        style="width:100%"></van-button>
        <!--  -->
        <div class="register">
          <button @click="RegisterHandler">
            
          </button>
        </div>
        <!-- / -->
      </div>
    </div>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
  data(){
    return {
      form:{
        type:'customer'
      }
    }
  },
  methods:{
    ...mapActions("user",["login"]),
    // 
    RegisterHandler(){
      this.$router.push({path:'/register'})
    },
    // 
    loginHandler(){
      this.login(this.form)
      .then(()=>{
        //
        this.$router.push({path:'/manager/home'})
      })
    }
  }
}
</script>
<style scoped>
.register{
  margin:1em;
  font-size:12px;
  color:green;
}
.register button{
  border:none
}
.header {
  height: 140px;
  padding: 50px;
  color: #ffffff;
  background-color: #1659a0;
  text-align: center
}
.header .logo {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  border: 1px solid #efefef;
  margin: 0 auto;
  color:#efefef;
  font-weight: bold;
  margin-bottom: 1em;
  font-size: 1.5em
}
.header .title {
  font-size: 24px;
}
</style>