<template>
    <div> 
        <briup-fullpagelayout title="" @back="backHandler">
            <div class="address" >
                <!--  -->
                <div class="list">
                    <briup-address-item v-for="address in conformAddresses" :key="address.id" :data="address"></briup-address-item>
                </div>
                <!-- / -->
                <!--  -->
                <van-button @click="addAddressHandler()" size="large" type="warning"></van-button>
                <!-- / -->
            </div>
        </briup-fullpagelayout>
    </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
export default {
    data() {
        return{
            
        }
    },
    computed: {
        ...mapState('address',['conformAddresses'])
    },
    created() {
        this.findAllAddresses();
    },
    methods: {
        ...mapActions('address',['findAllAddresses']),
        // 
        backHandler(){
            this.$router.push({path:'/manager/user'});
        },
        // 
        addAddressHandler(){
            this.$router.push({path:'/manager/address_add'})
        }
    }
}
</script>

<style scoped>
    .title{
        border-bottom: 1px solid #ccc
    }
    .address {
        background-color: #f1f1f1;
        padding-bottom: .2em;
    }
</style>