<template>
    <div>
        <!--  -->
        <briup-fullpagelayout title="" @back="backHandler">
            <div class="address_edit" >
                <div class="edit">
                    <!-- {{$route.query}} -->
                    <van-cell-group>
                        <van-field v-model="customerId" />
                        <van-field v-model="telephone" />
                        <van-field v-model="province" />
                        <van-field v-model="city" />
                        <van-field v-model="area" />
                        <van-field v-model="address" />
                    </van-cell-group>
                </div>
                <!--  -->
                <van-button 
                    @click="backListHandler()" 
                    size="large" 
                    type="warning"></van-button>
                <!-- / -->
            </div>
        </briup-fullpagelayout>
        <!-- / -->
    </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
export default {
    data() {
        return{
            customerId:'',
            telephone:'',
            province:'',
            city:'',
            area:'',
            address:'',
        }
    },
    computed: {
        ...mapState('address',['conformAddresses'])
    },
    created() {
        // this.$router.query
        this.customerId = this.$route.query.customerId,
        this.telephone = this.$route.query.telephone
        this.province = this.$route.query.province
        this.city = this.$route.query.city
        this.area = this.$route.query.area
        this.address = this.$route.query.address
    },
    methods: {
        ...mapActions('address',['findAllAddresses','SaveOrUpdateAddress']),
        // 
        backHandler(){
            this.$router.back();
        },
        // 
        backListHandler(){
            let form = {
                id:this.$route.query.id,
                customerId:this.customerId,
                telephone:this.telephone,
                province:this.province,
                city:this.city,
                area:this.area,
                address:this.address
            }
            this.$router.push({path:'/manager/address'})
            
            this.SaveOrUpdateAddress(form)
        }
       
    }
}
</script>

<style>
.van-col .one{
    border-bottom: 1px solid #ccc;
    padding:1em
}
</style>