import {get} from '../../http/axios'
export default {
    namespaced:true,
    state: {
        charge:''
    },
    mutations: {
        refreshRecharge(state,charge){
            state.charge = charge
        }
    },
    actions: {
        // 
        async CustomerRecharge({commit},params){
            let response = await get('/customer/recharge',params)
            commit('refreshRecharge',response.data)
        }
    }
}